/* --- keep your humanizeErrors, toE164, validatePassword helpers as-is --- */
function humanizeErrors(data: any): string {
  if (!data) return "–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ —Å–Ω–æ–≤–∞.";
  if (typeof data === "string") return data;
  if (typeof data.detail === "string") return data.detail;
  if (data.errors) {
    const errs: string[] = [];
    for (const [field, messages] of Object.entries(data.errors)) {
      const map: Record<string,string> = { email:"Email", phone:"–¢–µ–ª–µ—Ñ–æ–Ω", password:"–ü–∞—Ä–æ–ª—å", first_name:"–ò–º—è" };
      const label = map[field as string] || (field as string);
      if (Array.isArray(messages)) errs.push(`${label}: ${messages.join(", ")}`);
      else if (typeof messages === "string") errs.push(`${label}: ${messages}`);
    }
    return errs.length ? errs.join("\n") : "–û—à–∏–±–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏.";
  }
  const lines: string[] = [];
  for (const [k, v] of Object.entries(data)) {
    if (k === "message") continue;
    const map: Record<string,string> = { email:"Email", phone:"–¢–µ–ª–µ—Ñ–æ–Ω", password:"–ü–∞—Ä–æ–ª—å", first_name:"–ò–º—è" };
    const label = map[k] || k;
    if (Array.isArray(v)) lines.push(`${label}: ${v.join(", ")}`);
    else if (typeof v === "string") lines.push(`${label}: ${v}`);
  }
  return lines.length ? lines.join("\n") : "–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ —Å–Ω–æ–≤–∞.";
}

function toE164(input: string, defaultCountry: "KG" = "KG"): string | null {
  const digits = input.replace(/[^\d+]/g, "");
  if (digits.startsWith("+")) {
    const d = digits.replace(/[^\d]/g, "");
    return d.length >= 8 && d.length <= 15 ? digits : null;
  }
  const local = digits.replace(/\D/g, "");
  if (defaultCountry === "KG") {
    if (local.length === 9) return `+996${local}`;
    if (local.length === 10 && local.startsWith("0")) return `+996${local.slice(1)}`;
  }
  return null;
}

function validatePassword(password: string): { isValid: boolean; error: string | null } {
  if (password.length < 8) return { isValid:false, error:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 8 —Å–∏–º–≤–æ–ª–æ–≤" };
  if (!/[A-Z]/.test(password)) return { isValid:false, error:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É –∑–∞–≥–ª–∞–≤–Ω—É—é –±—É–∫–≤—É" };
  if (!/[a-z]/.test(password)) return { isValid:false, error:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É —Å—Ç—Ä–æ—á–Ω—É—é –±—É–∫–≤—É" };
  if (!/\d/.test(password))   return { isValid:false, error:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É —Ü–∏—Ñ—Ä—É" };
  return { isValid:true, error:null };
}

